import React, { Component } from 'react';
import Link from 'next/link';
import FacebookSVG from '../../static/svg/social/facebook.svg?include';
import LinkedinSVG from '../../static/svg/social/linkedin.svg?include';
import TwitterSVG from '../../static/svg/social/twitter.svg?include';

export default class Social extends Component {
  constructor(props) {
    super(props);
    this.platforms = [
      { icon: FacebookSVG,
        url: 'https://www.facebook.com/Hospall'
      },
      { icon: TwitterSVG,
        url: 'https://twitter.com/hospallhomecare'
      },
      { icon: LinkedinSVG,
        url: 'https://www.linkedin.com/company/hospall-private-homecare'
      }
    ];
  }
  shouldComponentUpdate(props) {
    console.log(props)
  }
  render() {
    const { align } = this.props;
    return (
      <div className="Social" style={{ textAlign: align || 'left' }}>
        { this.platforms.map((platform, index) =>
            <Link href={ platform.url } key={ index }>
              <a target='_blank' key={index} className="icon" dangerouslySetInnerHTML={{ __html: platform.icon }} />
            </Link>
          )
        }
      </div>
    );
  }
}